@font-face {
  font-family: "PT Sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

legend {
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.button {
  font-family: "PT Sans", sans-serif;
  display: block;
  position: relative;
  background-color: #81b3d2;
  padding: 15px 32px 18px 32px;
  color: #ffffff;
  font-size: 21px;
  font-weight: bold;
  line-height: 26px;
  border: none;
  max-width: 394px;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}

body {
  margin: 0;
}

.advantages__list {
  list-style: none;
  margin: 0;
  padding: 26px 22px 24px 22px;
  background-color: #eeeeee;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 0;
  }
}

.advantages__item:not(:last-child) {
  position: relative;
  margin-bottom: 50px;
}
@media (max-width: 767px) {
  .advantages__item:not(:last-child)::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: #d6d6d6;
    top: 157px;
  }
}

@media (min-width: 768px) {
  .advantages__list .advantages__item {
    padding: 41px 48px 42px 41px;
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .advantages__list .advantages__item {
    padding: 43px 55px 62px 55px;
  }
}

.advantages__title {
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  margin: 0;
  text-align: center;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .advantages__title {
    font-size: 21px;
    margin-bottom: 17px;
  }
}

.advantages__number,
.advantages__description {
  font-size: 14px;
  color: #333333;
  line-height: 21px;
  text-align: center;
}

.advantages__number {
  margin-bottom: 24px;
}
@media (min-width: 768px) {
  .advantages__number {
    margin-bottom: 20px;
    margin-top: 0;
  }
}

@media (min-width: 768px) {
  .advantages__description {
    margin: 0;
  }
}

.copyright {
  padding-top: 44px;
  margin-bottom: 27px;
  justify-self: end;
}

.copyright__description {
  font-size: 14px;
  line-height: 26px;
  display: inline-block;
  margin-left: auto;
  margin-right: 5px;
}

.copyright__link {
  display: inline-block;
  vertical-align: middle;
}
.copyright__link:hover {
  fill: #81b3d2;
}

.copyright__link svg:hover {
  fill: #81b3d2;
}
.copyright__link svg:active {
  opacity: 0.3;
}

.features {
  background-color: #ffffff;
}

.features__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

@media (min-width: 768px) {
  .features__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.features__item {
  padding-left: 69px;
  position: relative;
}
.features__item:first-child {
  padding-top: 35px;
}
@media (min-width: 768px) {
  .features__item:first-child {
    padding: 158px 35px 60px 36px;
  }
}
.features__item::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 38px;
  left: 20px;
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 768px) {
  .features__item::before {
    width: 76px;
    height: 75px;
    left: calc(50% - 36px);
  }
}
.features__item--house::before {
  background-image: url("../img/icon-house.svg");
  top: 26px;
}
@media (min-width: 768px) {
  .features__item--house::before {
    background-size: 75px 72px;
    top: 60px;
  }
}
.features__item--souvenir {
  grid-column: 3/4;
}
.features__item--souvenir::before {
  background-image: url("../img/icon-souvenir.svg");
  top: -13px;
}
@media (min-width: 768px) {
  .features__item--souvenir::before {
    background-size: 64px 75px;
    top: 60px;
  }
}
.features__item--food {
  grid-column: 2/3;
  grid-row: 1/2;
}
.features__item--food::before {
  background-image: url("../img/icon-food.svg");
  top: -7px;
}
@media (min-width: 768px) {
  .features__item--food::before {
    background-size: 76px 68px;
    top: 60px;
  }
}
@media (min-width: 768px) {
  .features__item {
    padding: 158px 35px 60px 36px;
  }
}

.features__title {
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  margin: 0;
}
@media (min-width: 768px) {
  .features__title {
    font-size: 21px;
    text-align: center;
    margin-bottom: 30px;
  }
}

.features__description {
  font-size: 14px;
  line-height: 21px;
  color: #333333;
}
@media (min-width: 768px) {
  .features__description {
    text-align: center;
    margin-bottom: 0;
  }
}
@media (min-width: 1200px) {
