  }
}

@media (min-width: 768px) {
  .main-advantages__container-grid--reverse {
    grid-column: 3/4;
  }
}

.main-nav {
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .main-nav {
    background-color: #ffffff;
  }
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
}

.main-nav__item {
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 5px;
}
.main-nav__item:disabled {
  background-color: #e5e5e5;
}
@media (max-width: 767px) {
  .main-nav__item {
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
    border-bottom: 1px solid #678fa8;
  }
  .main-nav__item:hover {
    background-color: #669ec0;
  }
  .main-nav__item:active {
    background-color: #5496bd;
    opacity: 0.3;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    font-size: 14px;
    line-height: 26px;
  }
  .main-nav__item:nth-child(4n-2) {
    margin-right: 30%;
  }
}

@media (min-width: 768px) {
  .main-nav__item--active .main-nav__link {
    color: #766357;
  }
  .main-nav__item--active .main-nav__link:hover {
    color: #604e43;
  }
  .main-nav__item--active .main-nav__link:active {
    opacity: 0.3;
  }
}
.main-nav__link {
  color: #ffffff;
  text-decoration: none;
}
@media (min-width: 768px) {
  .main-nav__link {
    color: #000000;
    width: 20%;
    padding-top: 16px;
    padding-bottom: 14px;
  }
  .main-nav__link:active {
    opacity: 0.3;
  }
  .main-nav__link:hover {
    color: #81b3d2;
  }
}

.main-nav__toggle {
  position: absolute;
  width: 56px;
  height: 56px;
  top: 224px;
  right: 0;
  z-index: 2;
  display: block;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 22px;
  height: 4px;
  top: 17px;
  left: 20px;
  background-color: #81b3d2;
  box-shadow: 0 7px 0 0 #81b3d2, 0 14px 0 0 #81b3d2;
}

.main-nav--opened .main-nav__toggle {
  top: 0;
}
.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  z-index: 2;
  width: 28px;
  height: 4px;
  background-color: #ffffff;
  top: 24px;
  left: 16px;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
  box-shadow: none;
}

@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--closed .main-nav__toggle {
    top: 0;
  }
}
