.logo-form {
  background-image: url("../img/intro-triangle.svg"), url("../img/form-mobile-background.jpg");
  min-width: 320px;
  height: 158px;
  background-repeat: no-repeat;
  background-position-x: 50%;
  background-position-y: 100%;
  background-size: cover;
  margin-top: -28px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/intro-triangle.svg"), url("../img/form-mobile-background@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    background-image: url("../img/intro-triangle.svg"), url("../img/background-main.jpg");
    height: 290px;
    margin-top: 0;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/intro-triangle.svg"), url("../img/background-main@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    height: 301px;
  }
}

.logo-form__image {
  display: block;
  margin-right: auto;
  margin-left: auto;
  padding-top: 52px;

  @media (min-width: $tablet-width) {
    width: 456px;
    height: 77px;
    padding-top: 95px;
    margin-bottom: 50px;
  }
}
